<template>
<div class="">
  <div class="main">
    <div class="item" v-for="(item,index) in lists" :key="index" @click="toDetail(item.id)">
      <div class="item-top">
        <div class="pic">
          <img :src="item.image" />
        </div>
        <div class="txt">
          <h1 class="name">{{item.name}}</h1>
          <div class="grade">
            {{item.grade}}分 <span class="rank">{{item.rank}}</span>
          </div>
          <div class="talk">
            {{item.talk}}条评论
          </div>
          <div class="distance">
            距离您{{item.distance}}公里
          </div>
          <div class="">
            {{}}
          </div>
        </div>
      </div>
    </div>
  </div>
  <bottom-menu></bottom-menu>
  <div id="allmap">

  </div>
</div>
</template>

<script>
import {
  MessageBox
} from 'mint-ui';
import BottomMenu from '../menu/Menu.vue'
import BMap from 'BMap'
export default {
  name: "Venue",
  data() {
    return {
      location: null,
      lists: [{
          id: "1",
          name: "成吉思汗",
          image: require("../../assets/images/test1.jpg"),
          grade: 50,
          rank: "很差",
          talk: 3,
          distance: 14.2
        },
        {
          id: "2",
          name: "成吉思汗",
          image: require("../../assets/images/test2.jpg"),
          grade: 50,
          rank: "很差",
          talk: 3,
          distance: 14.2
        },
        {
          id: "3",
          name: "成吉思汗",
          image: require("../../assets/images/test3.jpg"),
          grade: 50,
          rank: "很差",
          talk: 3,
          distance: 14.2
        }
      ]
    }
  },
  methods: {
    toDetail(id) {
      this.$router.push('/venue/detail/' + id)
    },
    addressDetail() { //获取地理位置
      alert("更新成功！！！")
      var map = new BMap.Map("allmap");
      var point = new BMap.Point(116.331398, 39.897445);
      map.centerAndZoom(point, 12);

      var geolocation = new BMap.Geolocation();
      // 开启SDK辅助定位
      geolocation.enableSDKLocation();
      geolocation.getCurrentPosition(function(r) {
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          var mk = new BMap.Marker(r.point);
          map.addOverlay(mk);
          map.panTo(r.point);
          alert('您的位置：' + r.point.lng + ',' + r.point.lat);
        } else {
          alert('failed' + this.getStatus());
        }
      });
    }
  },
  components: {
    BottomMenu
  },
  created() {
    this.$store.state.menuData = "场馆中心"
  },
  mounted() {
    this.addressDetail();
  }

}
</script>

<style scoped>
#allmap {
  height: 350px;
margin-bottom: 100px;
}

.main {
  padding-bottom: 50px;
}

.item {
  margin-bottom: 10px;
  background: #fff;
  padding: 10px;
}

.item-top {
  display: flex;
}

.pic {
  width: 60px;
  height: 60px;
  margin-right: 10px;

}

.pic img {
  width: 100%;
  height: 100%;
  display: block;
}

.txt {
  flex: 1;
}

.name {
  margin: 5px 0;
}

.desc {
  font-size: 14px;
  color: #777
}

.content {
  padding: 10px 0;
  line-height: 22px;
}
</style>



