<template>
<div class="">
  <mt-header title="注册">
    <router-link to="/loginStudent" slot="left">
      <mt-button icon="back"></mt-button>
    </router-link>
  </mt-header>
  <div class="main">
    <div class="input-group">
      <mt-field placeholder="请输入手机号" type="tel" v-model="telephone"></mt-field>
    </div>
    <div class="input-group">
      <mt-field v-model="captcha" @click.native.capture="getCode" placeholder="请输入验证码">{{text}}
      </mt-field>
    </div>
    <div class="input-group">
      <mt-field placeholder="请输入密码" type="password" v-model="password"></mt-field>
    </div>
    <div class="input-group">
      <mt-field placeholder="请再次输入密码" type="password" v-model="repeatPwd"></mt-field>
    </div>
    <div class="button" @click="register">注册</div>
  </div>
</div>
</template>
<script>
export default {
  name: "Register",
  data() {
    return {
      telephone: "",
      password: "",
      repeatPwd: "",
      captcha: "",
      text: "获取验证码",
      timer: null
    }
  },
  methods: {
    getCode() {
      if (this.timer == null) {
        this.codeTimer()
      }
    },
    codeTimer() {
      let seconds = 10
      this.text = "10秒后重新获取"
      this.timer = setInterval(() => {
        seconds--
        this.text = seconds + "秒后重新获取"
        if (seconds == 0) {
          clearInterval(this.timer)
          this.timer = null
          this.text = "点击重新获取"
        }
      }, 1000)
    },
    register(){
      this.$toast({
        message: '注册成功,稍后跳转！',
        position: 'bottom',
        iconClass:'',
        duration: 3000
      });
      setTimeout(()=>{
        this.$router.push("/loginStudent")
      },2000)
    }
  }
}
</script>
<style scoped>
.mint-header {
  background: none;
  color: #000;
}

.main {
  margin: 0 20px;
}

.input-group {
  border-bottom: 1px solid #000;
}
.button {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: #000;
  color: #fff;
  margin-top: 10px;
}
</style>



